import React, { Component } from 'react';
import styles from './styles.css';
import Logo from './Logo';
import Nav from './Nav';

export default class Header extends Component {
  render() {
    return (
      <div className={styles.container}>
        <div className={styles.content}>
          <Logo />
          <Nav />
        </div>
      </div>
    );
  }
}



