import React, { Component } from 'react';
import Link from 'components/Link';
import styles from './nav.css';

export default class Nav extends Component {
  render() {
    return (
      <div className={styles.container}>
        <div className={styles.contact}>
          <a className={styles.phone} href="tel:18004609735">
            <div className={styles.call}>Call us Today!</div>
            <div className={styles.number}>1.800.460.9735</div>
          </a>
          <div className={styles.social} />
          <div className={styles.social} />
          <div className={styles.social} />
        </div>
        <div className={styles.nav}>
          <Link className={styles.link} to="/about">About</Link>
          <Link className={styles.link} to="/safety">Safety</Link>
          <Link className={styles.link} to="/careers">Careers</Link>
          <Link className={styles.link} to="/tool-talk">Tool Talk</Link>
          <Link className={styles.link} to="/contact">Contact</Link>
        </div>
      </div>
    );
  }
}



